
.personal .personal__block {
  position: relative;
  margin-bottom: 9rem;
}
.personal .personal__block__image {
    width: 100%;
    display: block;
    border: 10px solid #73e47a;
}
.personal .personal__block__title {
    letter-spacing: 2px;
    font-size: 4rem;
    font-family: "Archivo Black", sans-serif;
    position: absolute;
    top: -3.5rem;
    z-index: -1;
    color: #73e47a;
    text-transform: uppercase;
}
.personal .personal__block__description {
    width: 100%;
    display: block;
    max-width: 800px;
    margin: 0 auto;
    color: #FFF;
    line-height: 135%;
    font-size: 1.2rem;
    padding: 25px 40px;
    background: #73e47a;
}
.personal .personal__block__description p {
      margin: 0 0 20px 0;
}
.personal .personal__block__description p:last-of-type {
        margin-bottom: 0;
}
.personal .personal__block__description__title {
      font-size: 1.5rem;
      font-family: "Archivo Black", sans-serif;
}



