
@import url("https://fonts.googleapis.com/css?family=Archivo+Black|Quicksand");
body {
  margin: 0;
  padding: 0;
  letter-spacing: 1px;
  font-family: "Quicksand", sans-serif;
}
body * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
body *, body *:before, body *:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
body .website {
    /*padding: 15px;*/
    width: 100%;
    height: 100vh;
    overflow: auto;
    position: relative;
    border: 15px solid #73e47a;
    /*padding: 75px 15%;*/
}
body .website #top-canvas-line {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0;
      z-index: 11;
      width: 100%;
      position: fixed;
      pointer-events: none;
      background: transparent;
      -webkit-transition: opacity 300ms ease-in-out;
      transition: opacity 300ms ease-in-out;
}
body .website #top-canvas-line.active {
        opacity: 1;
}
body .container {
    display: block;
    margin: 0 auto;
    max-width: 1200px;
}
body .global_title {
    position: relative;
    margin: 8rem 0 8rem;
}
body .global_title p {
      margin: 0;
      font-size: 3rem;
      display: inline-block;
}
body .global_title p span {
        z-index: 4;
        background: #fff;
        padding: 0 20px 0 0;
        position: relative;
        display: inline-block;
        color: #73e47a;
}
body .global_title p:before {
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        content: '';
        z-index: 1;
        position: absolute;
        background: #73e47a;
}
body .global_title p:after {
        top: 75%;
        bottom: 20%;
        left: 0;
        width: 10%;
        content: '';
        z-index: 2;
        position: absolute;
        background: #FFF;
        -webkit-animation: title-animation 2s infinite;
                animation: title-animation 2s infinite;
}
@-webkit-keyframes title-animation {
0% {
    left: 0;
}
100% {
    left: 100%;
}
}
@keyframes title-animation {
0% {
    left: 0;
}
100% {
    left: 100%;
}
}



