
.description {
  border-bottom: 1px solid #dadada;
}
.description .description__wrapper {
    display: block;
    margin: 0 auto;
    width: 100%;
    z-index: 4;
    max-width: 600px;
    padding: 4rem 0 12rem;
    position: relative;
}
.description .description__wrapper__white {
      padding: 20px 40px;
      background: #FFF;
}
.description .description__wrapper__white p {
        margin: 0;
        font-size: 1.1rem;
        color: #8d8d8d;
        line-height: 180%;
        margin-bottom: 20px;
}
.description .description__wrapper__white p strong {
          color: #000;
}
.description .description__wrapper__white p:last-of-type {
          margin-bottom: 0;
}
.description .description__wrapper__white:before {
        content: '';
        width: 80px;
        height: 2px;
        position: absolute;
        left: 50%;
        bottom: 10rem;
        background: #8d8d8d;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
}



