
.hero {
  width: 100%;
  height: 100vh;
  position: relative;
  min-height: 400px;
}
.hero .hero__name {
    top: 50%;
    left: 50%;
    z-index: 2;
    text-align: center;
    position: absolute;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
}
.hero .hero__name h2 {
      opacity: 0;
      color: #FFF;
      margin: 0;
      font-size: 48px;
      letter-spacing: 3px;
      display: inline-block;
      -webkit-transition: all 350ms ease-out;
      transition: all 350ms ease-out;
}
.hero .hero__name h2 span {
        z-index: 5;
        opacity: 0;
        color: #73e47a;
        display: block;
        position: relative;
        padding: 10px 55px;
        -webkit-transition: all 30ms 700ms ease-out;
        transition: all 30ms 700ms ease-out;
}
.hero .hero__name h2:before, .hero .hero__name h2:after {
        content: '';
        top: 0;
        left: 0;
        width: 0;
        bottom: 0;
        position: absolute;
}
.hero .hero__name h2:before {
        z-index: 1;
        background: #73e47a;
        -webkit-transition: all 700ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
        transition: all 700ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.hero .hero__name h2:after {
        z-index: 2;
        background: #FFF;
        -webkit-transition: all 500ms 700ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
        transition: all 500ms 700ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.hero .hero__name__subtitle {
      opacity: 0;
      z-index: 1;
      position: relative;
      font-family: "Archivo Black", sans-serif;
      letter-spacing: 3px;
      -webkit-transform: translateY(-20px);
              transform: translateY(-20px);
      text-transform: uppercase;
      -webkit-transition: all 700ms 200ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
      transition: all 700ms 200ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.hero .hero__name__subtitle small {
        letter-spacing: 2px;
}
.hero .hero__canvas {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.hero--pre_active .hero__name h2 {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.hero--pre_active .hero__name h2 span {
      opacity: 1;
      -webkit-transition-delay: 900ms !important;
              transition-delay: 900ms !important;
}
.hero--pre_active .hero__name h2:before, .hero--pre_active .hero__name h2:after {
      width: 100%;
      left: 0;
      right: 0;
}
.hero--active .hero__name h2 {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.hero--active .hero__name h2 span {
      color: #FFF;
      opacity: 1;
      -webkit-animation: all 1s 0s ease-out;
              animation: all 1s 0s ease-out;
}
.hero--active .hero__name h2:before {
      width: 100%;
}
.hero--active .hero__name h2:after {
      left: inherit;
      right: 0;
      width: 0;
}
.hero--active .hero__name__subtitle {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}



