
.contact {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  position: relative;
  color: #FFF;
  text-align: center;
  background: #73e47a;
}
.contact .contact__center {
    top: 50%;
    left: 0;
    right: 0;
    width: 100%;
    max-width: 600px;
    position: absolute;
    text-align: center;
    margin: 0 auto;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.contact .contact__center p {
      font-size: 2rem;
}
.contact .contact__center p a {
        color: #FFF;
        font-family: "Archivo Black", sans-serif;
        text-decoration: none;
}
.contact .contact__center p.twitter {
      color: #487ac6;
}
.contact .contact__center p.twitter a {
        color: #487ac6;
}
.contact .contact__center p.small {
      font-size: 1rem;
}
.contact .contact__center p.small a {
        font-family: "Quicksand", sans-serif;
}
.contact .contact__center p.small a:hover {
          text-decoration: underline;
}
.contact .contact__bottom {
    bottom: 5%;
    left: 0;
    right: 0;
    position: absolute;
    text-align: center;
}
.contact .contact__bottom p {
      font-size: 1.5rem;
      margin: 0;
}
.contact .contact__triangle {
    width: 0;
    height: 0;
    pointer-events: none;
    border-bottom: 100vh solid rgba(255, 255, 255, 0.1);
    border-right: 100vw solid transparent;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 0;
}



