.gradient-4 {
  background-color: #000;
  background: linear-gradient(-45deg, #000 0, #444 50%, #888 100%);
  /*background: linear-gradient(-45deg, $stripe-color 25%, transparent 25%, transparent 50%, $stripe-color 50%, $stripe-color 75%, transparent 75%, transparent);*/
  /*@include background-image(linear-gradient(-45deg, $stripe-color 25%, transparent 25%, transparent 50%, $stripe-color 50%, $stripe-color 75%, transparent 75%, transparent));*/
  background-size: 100% 100%;
}
.single-work {
  z-index: 99;
  opacity: 0;
  position: fixed;
  pointer-events: none;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.single-work .single-work__animation__content {
    z-index: 5;
    opacity: 0;
    position: absolute;
    left: 0;
    right: 0;
    top: 2rem;
    bottom: 2rem;
    overflow: auto;
    -webkit-transition: all 350ms 1000ms ease-in-out;
    transition: all 350ms 1000ms ease-in-out;
}
.single-work .single-work__animation__content__close {
      color: #FFF;
      font-family: "Archivo Black", sans-serif;
      top: 1rem;
      right: 2rem;
      z-index: 10;
      cursor: pointer;
      position: fixed;
      font-size: 7rem;
}
.single-work .single-work__animation__content__title {
      color: #FFF;
      text-align: center;
      padding: 10rem 0 4rem;
      font-family: "Archivo Black", sans-serif;
      font-size: 5rem;
      letter-spacing: 3px;
      text-transform: uppercase;
}
.single-work .single-work__animation__content__gif {
      width: 100%;
      text-align: center;
}
.single-work .single-work__animation__content__gif img {
        margin: 0 auto;
        display: block;
}
.single-work .single-work__animation__content__url {
      width: 100%;
      text-align: center;
      margin: 4rem 0 6rem;
}
.single-work .single-work__animation__content__url a {
        background: #FFF;
        padding: 20px 40px;
        font-family: "Archivo Black", sans-serif;
        font-size: 1.8rem;
        letter-spacing: 1px;
        text-transform: uppercase;
        display: inline-block;
        text-decoration: none;
}
.single-work .single-work__animation__content__grid {
      width: 100%;
      font-size: 0;
      margin: 3rem 0;
      background: #FFF;
      padding: 10px 20px;
      position: relative;
}
.single-work .single-work__animation__content__grid__description {
        width: 50%;
        padding: 20px;
        display: inline-block;
        vertical-align: middle;
        font-size: 1rem;
}
.single-work .single-work__animation__content__grid__extra {
        width: 49.5%;
        display: inline-block;
        vertical-align: middle;
}
.single-work .single-work__animation__content__grid__extra img {
          max-width: 80%;
          display: block;
          margin: 0 auto;
}
.single-work .single-work__animation__columns {
    height: 100vh;
    z-index: 1;
    width: 33.33%;
    position: absolute;
    border: 1px solid #FFF;
    background: #73e47a;
    -webkit-transition: all 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
    transition: all 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
}
.single-work .single-work__animation.active .single-work__animation__content {
    opacity: 1;
}
.single-work .single-work__animation.active .single-work__animation__columns {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.single-work.active {
    opacity: 1;
    pointer-events: all;
}
.single-work .single-work__animation .single-work__animation__columns:nth-of-type(2) {
  left: 33.33%;
  -webkit-transition-delay: 250ms;
          transition-delay: 250ms;
}
.single-work .single-work__animation .single-work__animation__columns:nth-of-type(3) {
  left: 66.66%;
  -webkit-transition-delay: 500ms;
          transition-delay: 500ms;
}
.work .work__recient-work {
  width: 100%;
  /*padding: 5rem 0;*/
}
.work .work__recient-work__element {
    width: 100%;
    clear: both;
    height: 265px;
    position: relative;
}
.work .work__recient-work__element__square {
      /*width: 100%;*/
      max-width: 48%;
      padding: 40px 80px;
      position: relative;
      cursor: pointer;
      margin-bottom: 6rem;
      /*overflow: hidden;*/
}
.work .work__recient-work__element__square__title {
        z-index: 5;
        font-family: "Archivo Black", sans-serif;
        color: #FFF;
        position: relative;
        font-size: 2rem;
        padding: 1rem 0 0;
        letter-spacing: 2px;
}
.work .work__recient-work__element__square__sub-title {
        z-index: 5;
        font-family: "Quicksand", sans-serif;
        color: #FFF;
        position: relative;
        font-size: 1.5rem;
        margin-bottom: 2rem;
        padding: 0.5rem 0 1rem;
        letter-spacing: 2px;
}
.work .work__recient-work__element__square__resume {
        z-index: 5;
        font-size: 1.3rem;
        font-family: "Quicksand", sans-serif;
        position: relative;
        float: right;
        -webkit-transform: translateX(6.5rem);
                transform: translateX(6.5rem);
        width: 300px;
}
.work .work__recient-work__element__square__resume__background {
          z-index: 2;
          position: relative;
          padding: 10px 20px;
          text-align: center;
          background: #FFF;
}
.work .work__recient-work__element__square__resume:before {
          left: 0;
          right: 0;
          bottom: 0;
          top: 0;
          z-index: -1;
          background: rgba(0, 0, 0, 0.5);
          -webkit-transform: translate3d(10px, 10px, 0);
                  transform: translate3d(10px, 10px, 0);
          content: '';
          position: absolute;
}
.work .work__recient-work__element__square__gradient {
        z-index: 1;
        opacity: 0.5;
        /*@include striped-bg(#000);*/
        -webkit-transition: opacity 350ms ease-out;
        transition: opacity 350ms ease-out;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        position: absolute;
}
.work .work__recient-work__element__square:before {
        content: '';
        z-index: 5;
        border: 2px solid #FFF;
        top: 20px;
        right: 30px;
        bottom: 20px;
        left: 30px;
        position: absolute;
}
.work .work__recient-work__element__square:after {
        content: '';
        z-index: -1;
        border: 5px solid #73e47a;
        /*transform: scale(2);*/
        top: -25%;
        width: 50%;
        height: 150%;
        position: absolute;
}
.work .work__recient-work__element__square:hover .work__recient-work__element__square__gradient {
        opacity: 1;
}
.work .work__recient-work__element:nth-of-type(2n + 1) .work__recient-work__element__square {
  float: left;
}
.work .work__recient-work__element:nth-of-type(2n) .work__recient-work__element__square {
  float: right;
}



